.vux-header-fade-in-right-enter {
  -webkit-animation: fadeinR .5s;
  animation: fadeinR .5s;
}

.vux-header-fade-in-left-enter {
  -webkit-animation: fadeinL .5s;
  animation: fadeinL .5s;
}

@-webkit-keyframes fadeinR {
  0% {
    opacity: 0;
    -webkit-transform: translateX(1.066667rem);
    transform: translateX(1.066667rem);
  }

  100% {
    opacity: 1;
